@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
html,body{margin:0;padding:0;box-sizing:border-box;font-family:Roboto;font-size:18px}html *,html *::after,html *::before,body *,body *::after,body *::before{box-sizing:inherit}
body{background-image:url(/Portfolio/static/media/Bckgrnd_Neutre.b336b269.jpg)}.Appli{margin-top:2%}.CarouselContainer>img{width:30%}.sideShow{background:linear-gradient(0deg, #454545 0%, #999999 100%);border-radius:20px 20px 20px 20px;padding:2%;color:#fff;text-align:center;font-family:"Anton"}
.logo{margin-bottom:2%}.liNorm{margin:0px 40px 40px 60px;font-size:48px;background:linear-gradient(to right, white 50%, rgba(255, 0, 0, 0) 50%);background-size:200% 100%;background-position:right bottom;transition:background-position .5s ease;border-radius:20px 20px 20px 20px}.navigation{text-align:center;grid-area:2/1/6/4;background-color:#999;border-radius:20px 20px 20px 20px;background:#454545;background:linear-gradient(0deg, #454545 0%, #999999 100%);margin:5%}.navigation>ul{list-style-type:none;margin-top:40px;padding:0px}.logo{padding-top:5px;width:140px;height:140px}.navigation p:hover{background-position:left bottom}a:link{text-decoration:none;color:#000}a:visited{text-decoration:none;color:#000}.menuActif{background:#000}.menuActif>a{color:#fff}.navActive:visited{color:#fff;font-size:62px}.navActive>p{background:#000;border-radius:20px 20px 20px 20px}
.acceuil_contenu{background:linear-gradient(0deg, #454545 0%, #999999 100%);border-radius:20px 20px 20px 20px;padding:2%}.contenu{font-size:2em;color:#fff;font-family:"Anton",sans-serif}.contenu>p{margin-bottom:10%;font-size:.8em}
.aPropos_contenu{margin:10px;border-radius:20px 20px 20px 20px;height:90%;background:linear-gradient(0deg, #454545 0%, #999999 100%);width:60%;padding:8%}.contenu{grid-area:2/6/6/11;color:#fff;font-family:"Anton",sans-serif;text-align:center;vertical-align:center}.contenu>p{margin-bottom:10%}.contenu>img{width:50%}
.meContacter_contenu{background:linear-gradient(0deg, #454545 0%, #999999 100%);margin:10px;border-radius:20px 20px 20px 20px;height:90%;height:90%}
.ListeDossiers{display:flex;list-style:none;padding:10px 20px;justify-content:center;flex-wrap:wrap}
.Realisation{width:750px;height:500px;margin:10px;color:#fff}.Realisation .couverture>img{width:750px;height:300px;object-fit:cover}.Realisation .info{padding:20px 15px}.Realisation .info h2{margin:0;padding:0;font-weight:normal;font-family:Roboto Slab}
